				window.addEventListener('resize', () => {
                    dianyingshujuerChart7.resize();
                },false);
            }
        });
      })
    },

//3
    dianyingshujuerChat8() {
      this.$nextTick(()=>{

        var dianyingshujuerChart8 = echarts.init(document.getElementById("dianyingshujuerChart8"),'macarons');
        this.$http({
            url: "dianyingshujuer/group/dianyingshizhang",
            method: "get",
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
					if(this.boardBase&&i==this.boardBase.barNum){
						break;
					}
                    xAxis.push(res[i].dianyingshizhang);
                    yAxis.push(parseFloat((res[i].total)));
                    pArray.push({
                        value: parseFloat((res[i].total)),
                        name: res[i].dianyingshizhang
                    })
                }
                var option = {};
				let titleObj = this.bar.title
				titleObj.text = ''
				
				const legendObj = this.bar.legend
				
				let xAxisObj = this.bar.xAxis
				xAxisObj.type = 'category'
				xAxisObj.data = xAxis
                
				
				let yAxisObj = this.bar.yAxis
				yAxisObj.type = 'value'
				
				let seriesObj = {
                        data: yAxis,
                        type: 'bar'
                    }
				seriesObj = Object.assign(seriesObj , this.bar.series)
				const gridObj = this.bar.grid
				let tooltipObj = {
					trigger: 'item',
					formatter: '{b} : {c}'
				}
				tooltipObj = Object.assign(tooltipObj , this.bar.tooltip?this.bar.tooltip:{})
                option = {
					backgroundColor: this.bar.backgroundColor,
					color: this.bar.color,
                    title: titleObj,
					legend: legendObj,
                    tooltip: tooltipObj,
					grid: gridObj,
                    xAxis: xAxisObj,
                    yAxis: yAxisObj,
                    series: [seriesObj]
                };
                // 
                dianyingshujuerChart8.setOption(option);
				

                  //
				window.addEventListener('resize', () => {
                    dianyingshujuerChart8.resize();
                },false);
            }
        });
      })
    },


  }
};
